/**
 * @description indexOf
 * @param arr [] 数组
 * @param ele never 查询的元素
 * @param fromIndex number|undefined 开始查找的位置
 */
export default (function (arr, ele, fromIndex) {
    var len = arr.length;
    if (len === 0)
        return -1;
    var index = fromIndex || 0;
    var i = Math.max(index >= 0 ? index : arr.length - Math.abs(fromIndex), 0);
    while (i < len) {
        if (i in arr && arr[i] === ele)
            return i;
        i++;
    }
    return -1;
});
