import isObj from './isObj';
/**
 * @description 返回object自身可枚举属性
 * @param obj {object} 接收一个object数据
 * @returns Array
 */
export default (function (obj) {
    if (!isObj(obj))
        return [];
    var keys = [];
    for (var key in obj) {
        keys.push(key);
    }
    return keys;
});
