/**
 * @description 正则验证 使用单例
 */
var CheckRegExp = /** @class */ (function () {
    function CheckRegExp() {
    }
    CheckRegExp.getInstance = function () {
        if (!this.$instance)
            this.$instance = new CheckRegExp();
        return this.$instance;
    };
    /**
     * @description 匹配手机号码
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.mobile = function (v) {
        var reg = /^1[3|4|5|6|7|8|9][0-9]{9}$/;
        return reg.test(v);
    };
    /**
     * @description 匹配座机
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.telephone = function (v) {
        var reg = /^(0\d{2,3}-\d{7,8})(-\d{1,4})?$/;
        return reg.test(v);
    };
    /**
     * @description 匹配密码
     * 密码以字母开头，长度在6~18之间，只能包含字母、数字和下划线
     * @param v any
     * @return  boolean
     */
    CheckRegExp.prototype.password = function (v) {
        var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        return reg.test(v);
    };
    /**
     * @description 匹配QQ号码
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.qq = function (v) {
        var reg = /^[1-9][0-9]{4,9}$/;
        return reg.test(v);
    };
    /**
     * @description 匹配身份证号码
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.IDcard = function (v) {
        var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        return reg.test(v);
    };
    /**
     * @description 匹配邮政编码
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.postal = function (v) {
        var reg = /[1-9]\d{5}(?!\d)/;
        return reg.test(v);
    };
    /**
     * @description 匹配URL
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.url = function (v) {
        var reg = /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/;
        return reg.test(v);
    };
    /**
     * @description 匹配IP
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.IPv4 = function (v) {
        var reg = /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
        return reg.test(v);
    };
    /**
     * @description 匹配数字
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.number = function (v) {
        var reg = /^[0-9]$/;
        return reg.test(v);
    };
    /**
     * @description 匹配英文
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.en = function (v) {
        var reg = /^[a-zA-Z]+$/;
        return reg.test(v);
    };
    /**
     * @description 匹配中文
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.cn = function (v) {
        var reg = /^[\u4E00-\u9FA5]+$/;
        return reg.test(v);
    };
    /**
     * @description 匹配HTML标签
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.html = function (v) {
        var reg = /<("[^"]*"|'[^']*'|[^'">])*>/;
        return reg.test(v);
    };
    /**
     * @description 匹配邮箱地址
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.email = function (v) {
        var reg = /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/;
        return reg.test(v);
    };
    /**
     * @description 匹配账号合法
     * @param v any
     * @returns boolean
     */
    CheckRegExp.prototype.username = function (v) {
        var reg = /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/;
        return reg.test(v);
    };
    return CheckRegExp;
}());
var check = CheckRegExp.getInstance();
export default check;
